import { Component, OnInit, AfterViewInit } from '@angular/core';
import { BaseComponent } from '../../base/base.component';
import { BOTONES_FLUJO, PAIS, MODULO, CANAL, LENGUAJE, CANALES, GTM } from 'src/mbaas/mbaas.const';
import { IBeneficio } from 'src/mbaas/core/components/benefit-list/benefit-list.model';
import { CAT_BEN001 } from 'src/mbaas/mbaas.kind.const';

@Component({
  selector: 'mbaas-ben001',
  templateUrl: './ben001.component.html',
  styleUrls: ['./ben001.component.scss']
})
export class Ben001Component extends BaseComponent implements OnInit, AfterViewInit {
  beneficios: IBeneficio;

  ngOnInit() {
    const P = this.payload;
    this.obser.sendData(P.pais, PAIS);
    this.obser.sendData(P.modulo, MODULO);
    this.obser.sendData(P.canal, CANAL);
    this.obser.sendData(P.lenguaje, LENGUAJE);
    this.getBeneficios();
  }

  /* GTM */
  ngAfterViewInit(): void {
    this.gtmService.createGtm(
      window,
      CANALES[this.payload.canal] + '-Tarjeta MÃ³vil',
      GTM.PAGE_VIEW,
      '1. Beneficios y caracteristicas'
    );
  }

  getDataToBack(): () => { [key: string]: any } {
    return (): { [key: string]: any } => ({
      atras: BOTONES_FLUJO.ATRAS
    });
  }

  getDataToContinue(): () => { [key: string]: any } {
    return (): { [key: string]: any } => ({
      atras: BOTONES_FLUJO.CONTINUAR
    });
  }

  getBeneficios(): void {
    this.catalogo.data(CAT_BEN001, this.params([PAIS, LENGUAJE, MODULO])).subscribe(response => {
      this.beneficios = response ? response : [];
    });
  }

}
