import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'mbaas-button-continue',
  templateUrl: './button-continue.component.html',
  styleUrls: ['./button-continue.component.scss']
})
export class ButtonContinueComponent implements OnInit {
  // tslint:disable-next-line: no-inferrable-types
  @Input() disableIf: boolean = false;
  // tslint:disable-next-line: no-inferrable-types
  @Input() label: string = '';
  // tslint:disable-next-line: no-inferrable-types
  @Input() buttonClass: string = 'button button--primary';
  @Output() call = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  click(): void {
    this.call.emit();
  }
}


